
import { defineComponent } from "vue";
import Clickable from "@/components/Clickable.vue";

export default defineComponent({
  components: {
    Clickable
  },
  props: {
    text: String,
    subject: String
  },
  data() {
    return {
      copied: false
    };
  },
  methods: {
    async copy() {
      await window.navigator.clipboard.writeText(this.text || "");
      this.copied = true;
      window.setTimeout(() => (this.copied = false), 1000);
    }
  }
});
